header
{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container
{
    text-align: center;
    height: 100%;
    position: relative;
}

/******* pour la partie action ******/
.action
{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/******* Socials ******/

.header__socials
{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

/*** petit trait sous la barre ***/
.header__socials::after
{
    content: " ";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);    
}

/*** La Photo ***/
.moi
{
    background: linear-gradient(var(--color-primary), transparent);
    width: 19rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    /*overflow: hidden;*/
    /*padding: 5rem 1.5rem 1.5rem 1.5rem;*/
}


/**** barre de scrool personnalisée ****/
.scroll__down
{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ================ Ecran de taille moyenne ( tablette ) ============= */
@media screen and (max-width: 1024px)
{
    header
    {
        height: 68vh;
    }
}

/* ================ Ecran de taille petite ( téléphone ) ============= */
@media screen and (max-width: 600px)
{
    header
    {
        height: 100vh;

    }
    .header__socials, .scroll__down
    {
        display: none;
    }
}