@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header
{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container
{
    text-align: center;
    height: 100%;
    position: relative;
}

/******* pour la partie action ******/
.action
{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/******* Socials ******/

.header__socials
{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

/*** petit trait sous la barre ***/
.header__socials::after
{
    content: " ";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);    
}

/*** La Photo ***/
.moi
{
    background: linear-gradient(var(--color-primary), transparent);
    width: 19rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    /*overflow: hidden;*/
    /*padding: 5rem 1.5rem 1.5rem 1.5rem;*/
}


/**** barre de scrool personnalisée ****/
.scroll__down
{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ================ Ecran de taille moyenne ( tablette ) ============= */
@media screen and (max-width: 1024px)
{
    header
    {
        height: 68vh;
    }
}

/* ================ Ecran de taille petite ( téléphone ) ============= */
@media screen and (max-width: 600px)
{
    header
    {
        height: 100vh;

    }
    .header__socials, .scroll__down
    {
        display: none;
    }
}
nav
{
    background-color: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a
{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover
{
    background:rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}
.about__container
{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;

}

.about__me
{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent); /*l'arriere photo*/
    display: grid;
    place-items: center;
}

.about__me-img
{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-img:hover
{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}

.about__cards
{
    display: grid;
    grid-template-columns: repeat(3, 3fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card
{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover
{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon
{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5
{
    font-size: 0.95rem;
}

.about__card small
{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p
{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* ================ Ecran de taille moyenne ( tablette ) ============= */
@media screen and (max-width: 1024px)
{
   .about__container
   {
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
   }

   .about__me
   {
    width: 50%;
    margin: 2rem auto 4rem;
   }

   .about__content p
   {
    margin: 1rem 0 1;
   }
}

/* ================ Ecran de taille petite ( téléphone ) ============= */
@media screen and (max-width: 600px)
{
    .about__me
   {
    width: 65%;
    margin: 0 auto 3rem;
   }

   .about__cards
   {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
   }

   .about__content
   {
    text-align: center;
   }

   .about__content p
   {
    margin: 1.5rem 0;
   }
}

.competence__container
{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.competence__container > div
{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.competence__container > div:hover
{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.competence__container > div h4
{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.competence__content
{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.competence__detail
{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.competence__detail-icons
{
    margin-top: 6px;
    color: var(--color-primary);
}

/* ================ Ecran de taille moyenne ( tablette ) ============= */
@media screen and (max-width: 1024px)
{
   .competence__container
   {
    grid-template-columns: 1fr;
   }

   .competence__content > div
   {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
   }

   .competence__content
   {
    padding: 1rem;
   }
}

/* ================ Ecran de taille petite ( téléphone ) ============= */
@media screen and (max-width: 600px)
{
    .competence__container
    {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .competence__container > div
    {
        width: 100%;
        padding: 1rem 1rem;
    }    
}

/* ================ Ecran de tres petite taille( téléphone ) ============= */
@media screen and (max-width: 400px)
{
    .competence__container
    {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .competence__container > div
    {
        width: 100%;
        padding: 1rem 1rem;
    }    
}
.projects__container
{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.projects__item
{
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.projects__item:hover
{
    border-color: var(--color-primary-variant);
    background: transparent;
}

.projects__item-img
{
    border-radius: 1.5rem;
    overflow: hidden;
}

.projects__item h3
{
    margin: 1.2rem 0 2rem;
}

.projects__item-cta
{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/* ================ Ecran de taille moyenne ( tablette ) ============= */
@media screen and (max-width: 1024px)
{
    .projects__container
    {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}

/* ================ Ecran de taille petite ( téléphone ) ============= */
@media screen and (max-width: 600px)
{
  .projects__container
  {
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
}
.container.contact__container
{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options
{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option
{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover
{
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon
{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a
{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/******************* FORMULAIRE ******************/

form
{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, 
textarea
{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}

/* ================ Ecran de taille moyenne ( tablette ) ============= */
@media screen and (max-width: 1024px)
{
    .container.contact__container
    {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/* ================ Ecran de taille petite ( téléphone ) ============= */
@media screen and (max-width: 600px)
{
    .container.contact__container
    {
        width: var(--container-width-sm);
    }
}
footer
{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
}

footer a
{
    color: var(--color-bg);
}

.footer__logo
{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks
{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials
{
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a
{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover
{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright
{
    margin-bottom: 4rem;
    color: var(--color-bg);
}



/* ================ Ecran de taille petite ( téléphone ) ============= */
@media screen and (max-width: 600px)
{
    .permalinks
    {
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }

    .footer__socials
    {
        margin-top: 2.6rem;
    }
}
/*importation de font google poppins avec fonts.google.com*/


* /*tous les élément sur la page*/
{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root /*Css variables utilisable n'importe ou dans le projet*/
{
    --color-bg: #1f1f38; /* variable pour couleur back-ground*/
    --color-bg-variant: #2c2c6c; /*variable pour la nuance de couleur du baclground*/
    --color-primary: #6FFA49;/*#4db5ff;*/ /*variable couleur choses importantes*/
    --color-primary-variant: rgba(77,181,255,0.4); /*couleur pour les cards*/
    --color-white: #fff; /*couleur de l'ecriture*/
    --color-light: rgba(255,255,255,0.6); 

    --transition: all 400ms ease;

    /* == Rendre le site de bonnes tailles sur pc, tablette ou téléphone ==== */
    --container-width-lg: 75%; /*taille d'un container où seront les éléments*/
    --container-width-md: 86%;
    --container-widht-sm: 90%;
}

html
{
    scroll-behavior: smooth; /* pour scroller de façon fluide entre les sections*/
}

::-webkit-scrollbar /*barre pour scroller customiser (ici on l'enlève)*/
{
    display: none;
}

body 
{
    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg); /*utilisation de la variable définie plus haut*/
    color: #fff;
    color: var(--color-white); 
    line-height: 1.7;
    background-image: url(data:image/png;base64,);
}


.container
{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5
{
    font-weight: 500;
}

h1
{
    font-size: 2.5rem;
}

section /*tous les éléments sont dans des sections divs*/
{
    margin-top: 8rem;
}

section > h2, section > h5 /*toutes les sections possèdent deux tritres en h2 et h5*/
{
    text-align: center;
    color: rgba(255,255,255,0.6);
    color: var(--color-light);
}

section > h2
{
    color: #6FFA49;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light
{
    color: rgba(255,255,255,0.6);
    color: var(--color-light);
}

a
{
    color: #6FFA49;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition) ;
}

a:hover
{
    color: #fff;
    color: var(--color-white);
}

.btn
{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #6FFA49;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #6FFA49;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover
{
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary
{
    background: #6FFA49;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
}

img
{
    display: block;
    width: 100%;
    object-fit: cover; /*pour se modeler au container*/
}

/* ================ Ecran de taille moyenne ( tablette ) ============= */
@media screen and (max-width: 1024px)
{
    .container
    {
        width: 86%;
        width: var(--container-width-md);
    }
    section
    {
        margin-top: 6rem;
    }
}

/* ================ Ecran de taille petite ( téléphone ) ============= */
@media screen and (max-width: 600px)
{
    .container
    {
        width: var(--container-width-sm);
    }
    section > h2
    {
        margin-bottom: 2rem;
    }
}



